import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {UrlServeService} from './url-serve.service';
import {NzMessageService} from 'ng-zorro-antd';
import {Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class ResetHttpService {
  public restServer;
  headers = {
    headers: new HttpHeaders({
      'Content-Type': "application/x-www-form-urlencoded;charset=UTF-8"
    })
  };
  constructor(private http: HttpClient,private url:UrlServeService,private msg:NzMessageService,private router:Router) {
    this.restServer = this.url.commentBaseUrl;
  }
  public get(url, params?: Object, cb?: Function,err?:Function) {
    let httpParams = new HttpParams();
    console.log('get开始请求');
    const vm = this;
    if (params) {
      for (const key in params) {
        if (params[key] === false || params[key]) {
          httpParams = httpParams.set(key, params[key]);
        }
      }
    }
    if(sessionStorage.getItem('isLogin')){
      httpParams = httpParams.set("token",sessionStorage.getItem('token'));
    }
    console.log(vm.restServer + url);
    console.log(httpParams);
    vm.http.get(vm.restServer + url, {params: httpParams})
      .subscribe(data => {
        console.log('get请求结束');
          if(data['code'] === '99999'){
            vm.msg.error(data['message']);
            sessionStorage.clear();
            vm.router.navigate(['/login']);
          }else{
            cb(data);
          }

      },
        erro =>{
          console.log('get请求失败');
          err(erro);
        }
      );
  }

  public post(url, data?: Object, cb?: Function,err?:Function, options?: Object) {
    console.log('post开始请求');
    const vm = this;
    if(sessionStorage.getItem('isLogin')){
      data['token'] = sessionStorage.getItem('token');
    }
    console.log(this.transformRequest(data));
    vm.http.post(vm.restServer + url, this.transformRequest(data), this.headers)
      .subscribe(res => {
        console.log('post请求结束');
        if(res['code'] === '99999'){
          vm.msg.error(res['message']);
          sessionStorage.clear();
          vm.router.navigate(['/login']);
          return;
        }
          cb(res);

      },
        erro =>{
        console.log('post请求失败');
        err(erro);
        }
      );
  }
  transformRequest( data ) {
    var str = '';
    for( var i in data ) {
      str += i + '=' + data[i] + '&';
    }
    return str.substring(0,str.length-1);
  }
}
