import { Component, OnInit } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {ResetHttpService} from '../../share/util/reset-http.service';
import {UrlServeService} from '../../share/util/url-serve.service';
import {NzMessageService} from 'ng-zorro-antd';
declare var hex_md5:any;
@Component({
  selector: 'app-edit-parssword',
  templateUrl: './edit-parssword.component.html',
  styleUrls: ['./edit-parssword.component.scss']
})
export class EditParsswordComponent implements OnInit {
  constructor(private msg:NzMessageService,private fb: FormBuilder,private http:ResetHttpService,private router:Router,private url:UrlServeService) {
    this.validateForm = this.fb.group({
      oldPassword   : [ '', [ Validators.required ] ],
      password: [ '', [ this.checkPassword ] ],
      confirm : [ '', [ this.confirmValidator ] ]
    });
  }
  ngOnInit() { }

  validateForm: FormGroup;
  patt = /\w{6,12}/;

  //提交
  submitForm = ($event, value) => {
    var that = this;
    console.log(value);
    $event.preventDefault();
    for (const key in this.validateForm.controls) {
      this.validateForm.controls[ key ].markAsDirty();
      this.validateForm.controls[ key ].updateValueAndValidity();
    }
    this.http.get(this.url.resetPassword, {
      oldPassword:hex_md5(this.validateForm.controls['oldPassword'].value),
      password1:hex_md5(this.validateForm.controls['password'].value),
      password2:hex_md5(this.validateForm.controls['confirm'].value),
    },data=>{
      // console.log(data);
      if(data.code == '001000'){
        this.msg.success('修改成功！');
        sessionStorage.clear();
        that.router.navigate(['/login']);
      }else{
       this.msg.error(data.message);
      }
    },erro=>{
      console.log(erro);
    });
  };
  //取消
  cancleForm(e: MouseEvent): void {
    history.back();
  }
  //重新计算确认密码
  validateConfirmPassword(): void {
    setTimeout(() => this.validateForm.controls.confirm.updateValueAndValidity());
  }
  //再次验证密码
  confirmValidator = (control: FormControl): { [ s: string ]: boolean } => {
    if (!control.value) {
      return { required: true };
    } else if (control.value !== this.validateForm.controls.password.value) {
      return { confirm: true, error: true };
    }
  };
  //验证密码
  checkPassword = (control:FormControl): {[ s: string ]: boolean} => {
    if (!control.value) {
      return {required: true}
    } else if (!this.patt.test(control.value)) {
      return {error: true};
    }
  }
}
