import { Injectable } from '@angular/core';
import { NzModalService } from 'ng-zorro-antd';
import {Router} from '@angular/router';
import {HttpHeaders} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class UtilServeService {

  constructor(private modal: NzModalService,private router:Router) { }
  initPassword(): void {
    var modal = this.modal.confirm({
      nzTitle: '提示',
      nzContent: '初始密码123456',
      nzOnOk: () => new Promise((resolve, reject) => {
        history.back();
        modal.destroy();
      }).catch(() => console.log('Oops errors!'))
    });
  }
  goPage(option){
    if(option.data){
      var sendData = '';
      if(typeof option.data == 'object'){
        sendData = JSON.stringify(option.data);
      }else{
        sendData = option.data;
      }
      this.router.navigate([option.url],{queryParams :{data:sendData}});
    }else{
      this.router.navigate([option.url]);
    }

  }
}
