import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import {Router} from '@angular/router';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {HttpClient} from '@angular/common/http';
import '../../../assets/plugins/gVerify.js';
import {ResetHttpService} from '../../share/util/reset-http.service';
import {UrlServeService} from '../../share/util/url-serve.service';
import {UtilServeService} from '../../share/util/util-serve.service';
declare var GVerify:any;
declare var hex_md5:any;
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  form: FormGroup;
  loading = false;
  passWordPat = /\w{6,12}/;
  verifyCode;
  constructor(
    public fb: FormBuilder,
    private router: Router,
    public msg: NzMessageService,
    private modalSrv: NzModalService,
    private httpClient: ResetHttpService,
    private url:UrlServeService,
    private util:UtilServeService
  ) {
    // 创建一组表单控件
    this.form = fb.group({
      userName: [null, [Validators.required]],
      password: [null, [this.checkPassword]],
      qrCode:[null,[Validators.required]]
    });
    modalSrv.closeAll();

  }
  ngOnInit(){
    this.verifyCode = new GVerify('v_container');
  }
// 获得当前控件的值
  get userName() {
    return this.form.controls.userName;
  }
  get password() {
    return this.form.controls.password;
  }
  get qrCode() {
    return this.form.controls.qrCode;
  }


  // endregion

  submit() {
    // 如果username或者password无效 返回
    for (const key in this.form.controls) {
      this.form.controls[ key ].markAsDirty();
      this.form.controls[ key ].updateValueAndValidity();
    }
    if(!this.qrCode.value){
      return;
    }
    if(!this.verifyCode.validate(this.qrCode.value)){
      this.qrCode.setErrors({ "error" : true});
      console.log('验证码错误');
      return;
    }
    if (this.userName.invalid || this.password.invalid || this.qrCode.invalid) return;

    // 开启加载
    this.loading = true;

     this.httpClient.get(this.url.login,{login_name:this.userName.value,password:hex_md5(this.password.value)},data=>{
       console.log(data);
       //关闭加载
       this.loading = false;
       if(data.code === '001000'){
         // application data
         const res: any = data.response;
         const userInfo = JSON.stringify({
           name: res.name,
           is_super: res.is_super,
           role_code:res.role_code,
           password:hex_md5(this.password.value),
           username:this.userName.value,
           userId:res.userId
         });
         sessionStorage.setItem('user',userInfo);
         sessionStorage.setItem('token',res.token);
         //确认登录
         sessionStorage.setItem('isLogin','true');
         this.getSlider();
       }else{
         this.msg.error(data['message']);
       }
     },error=>{
       console.log(error);
       //关闭加载
       this.loading = false;
       this.msg.error('请求失败');
     })

  }
  // 验证密码
  checkPassword = (control: FormControl): {[ s: string ]: boolean} =>{
    if (!control.value) {
      return {required: true };
    } else if (!this.passWordPat.test(control.value)){
      return { error: true };
    }
  }
  //获取侧边栏
  getSlider(){
    this.httpClient.get(this.url.slide,{
      token:sessionStorage.getItem('token')
    },data=>{
      console.log(data);
      if(data.code == '001000'){
        // 初始化菜单
        sessionStorage.setItem('menu',JSON.stringify(data.response));
        //跳转
        this.router.navigate(['/']);
      }else{
        this.msg.error(data['message']);
      }
    },err=>{
      console.log(err);
    })
  }
}
