import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { NgZorroAntdModule, NZ_I18N, zh_CN } from 'ng-zorro-antd';
import { registerLocaleData } from '@angular/common';
import zh from '@angular/common/locales/zh';


import {LayoutModule} from './layout/layout.module';
import {PassportModule} from './passport/passport.module';
import {ShareModule} from './share/share.module';

//单页面
import {WelcomeComponent} from './pages/welcome/welcome.component';
import {LoginGuradService} from './login-gurad.service';
import { EditParsswordComponent } from './pages/edit-parssword/edit-parssword.component';
import {RouteReuseStrategy} from '@angular/router';
import {SimpleReuseStrategy} from './SimpleReuseStrategy/SimpleReuseStrategy';

registerLocaleData(zh);

@NgModule({
  declarations: [
    AppComponent,
    WelcomeComponent,
    EditParsswordComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    NgZorroAntdModule,
    LayoutModule,
    PassportModule,
    ShareModule
  ],
  providers: [{ provide: NZ_I18N, useValue: zh_CN } ,LoginGuradService],
  bootstrap: [AppComponent]
})
export class AppModule { }
